<template>
        <section>
            <div>
                <p>夏朝的兴起与灭忙<span>试听</span></p>
                <img src="" :alt="courseItem.type===1?'耳机':'锁头'" @click="goDetail(courseItem.type)">
            </div>
            <div>
                <span>
                   {{courseItem.name}}
                </span>
                <span>
                    {{courseItem.age}}人听过
                </span>
            </div>
        </section>
</template>
<script>
import { mapState } from "vuex";
import utils from "@/utils/index.js";
let util = new utils();
export default {
  name: "CourseListItem",
  data() {
    return {};
  },
  props: {
    courseItem: {
      type: Object,
      default: function() {
        return {};
      }
    }
  },
  computed: {
    ...mapState({
      name: state => state.name
    })
  },
  created() {
    console.log(util.getQueryString("name"));
  },
  methods: {
    goDetail(type) {
      switch (type) {
        case 1:
          this.$router.push({
            path: "/audioCourse"
          });
          break;
        case 2:
          this.$router.push({
            path: "/videoCourse"
          });
          break;
      }
    }
  }
};
</script>
<style lang="less" scoped>
section {
  height: 40px;
  line-height: 40px;
  & > :first-child {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    font-weight: 800;
    p {
      span {
        display: inline-block;
        margin-left: 10px;
      }
    }
  }
  & > :last-child {
    display: flex;
    justify-content: start;
    align-items: center;
    span {
      color: @middleGrey;
      font-size: 12px;
    }
    & > :first-child {
      display: inline-block;
      margin-right: 10px;
    }
  }
}
</style>



