<template>
  <div>
  
  
  
    <main>
  
  
  
      <section>
  
  
  
        <img src="" alt="banner">
  
  
  
        <p>课程中带你课程中带你课程中带你课程中带你课程中带你课程中带你课程中带你课程中带你</p>
  
  
  
        <h3>课程表</h3>
  
  
  
      </section>
      <div class="course-content">
        <section >
            课程中带你课程中带你课程中带你课程中带你课程中带你课程中带你课程中带你课程中带你课程中带你课程中带你课程中带你课程中带你课程中带你课程中带你课程中带你课程中带你
        </section>
      </div>

    </main>
      <footer>
      <p @click="goPurchase">
      立即购买
    </p>
    </footer>
  </div>
</template>
<script>
import Comment from "components/Comment";
export default {
  name: "share",
  data() {
    return {
      count: 3,
      commentList: [
        { name: 1, age: "32fd33s434adsa", type: 1 },
        { name: 31, age: "d", type: 2 },
        { name: 31, age: "32ffs324fd43sa423dsa", type: 3 },
        { name: 14, age: "3233324sadsa", type: 1 },
        { name: 421, age: "322fd44333sadsa", type: 3 },
        { name: 421, age: "3233sadsa", type: 2 }
      ]
    };
  },

  components: { Comment },

  created() {},

  methods: {
    toggleNav(para) {
      this.isShowContent = para ? true : false;
    },
    goPurchase() {
      this.$router.push({
        path: "/purchase"
      });
    }
  }
};
</script>
<style lang="less" scoped>
div {
  main {
    section {
      img {
        display: inline-block;

        width: 10rem;

        height: 4rem;
      }
    }

    .course-content {
      .nav {
        display: flex;

        width: 100%;

        height: 40px;

        line-height: 40px;

        border-top: 1px solid @grey;

        border-bottom: 1px solid @grey;

        li {
          width: 50%;

          align-items: center;

          justify-content: center;

          text-align: center;
        }
      }

      h3 {
        text-align: center;
      }

      table {
        width: 100%;
      }

      section {
        .introduence-list {
          li {
          }
        }
      }
    }
  }
  footer {
    position: fixed;
    bottom: 0;
    width: 10rem;
    height: 40px;
    background: yellow;
    p {
      width:100%;
      text-align: center;
    }
  }
}
</style>
 



