<template>
    <section>
        <div>
            <div>
                <img src="" alt="头像"><span>队长</span>
                <p>{{commentItem.name}}</p>
            </div>
            <div class="count-wrap">
                <img src="" alt="点赞图标">
                <span>{{commentItem.age}}</span>
            </div>
        </div>
    </section>
</template>
<script>
import { mapState } from "vuex";

import utils from "@/utils/index.js";

let util = new utils();

export default {
  name: "CourseListItem",

  data() {
    return {};
  },

  props: {
    commentItem: {
      type: Object,

      default: function() {
        return {};
      }
    }
  },

  computed: {
    ...mapState({
      name: state => state.name
    })
  },

  created() {
    console.log(util.getQueryString("name"));
  },

  methods: {
    goDetail(type) {
      switch (type) {
        case 1:
          this.$router.push({
            path: "/audioCourse"
          });

          break;

        case 2:
          this.$router.push({
            path: "/videoCourse"
          });

          break;
      }
    }
  }
};
</script>
<style lang="less" scoped>
section {
  img {
    width: 1rem;
    height: 1rem;
  }
  .count-wrap {
    display: flex;
    justify-content: flex-end;
    border-bottom: 1px solid @grey;
  }
}
</style>



