<template>
      <div class="purchase">
          <main>
              <section class="preview">
                  <img src="" alt="介绍">
                  <div>
                      <p>
                          <span>名称</span>
                          <span>历史课</span>
                      </p>
                       <p>
                          <span>名称</span>
                          <span>历史课</span>
                      </p> <p>
                          <span>名称</span>
                          <span>历史课</span>
                      </p>
                  </div>
              </section>
              <section class="sale-wrap">
                  <p>
                          <span>优惠卷</span>
                          <span>100</span>
                      </p>
                       <p>
                          <span>名称</span>
                          <span>历史课</span>
                      </p> <p>
                          <span>名称</span>
                          <span>历史课</span>
                      </p>
              </section>
              <section class="purchase-tip">
                <p>购买须知</p>
                <p>fsdsdfssssssrevgtrrrrrrrrrrrrrrrrr</p>
              </section>
          </main>
          <footer @click="goCourse">
              <p>
                  <span>189</span>元确认支付
              </p>
          </footer>
      </div>
</template>
<script>
import { mapState } from "vuex";
import utils from "@/utils/index.js";
let util = new utils();
export default {
  name: "purchase",
  data() {
    return {};
  },
  props: {
    courseItem: {
      type: Object,
      default: function() {
        return {};
      }
    }
  },
  computed: {
    ...mapState({
      name: state => state.name
    })
  },
  created() {
    console.log(util.getQueryString("name"));
  },
  methods: {
    goCourse(){
        this.$router.push({
            path:"/CourseList"
        })
    }
  }
};
</script>
<style lang="less" scoped>
.purchase {
  main {
    .preview {
      display: flex;
      justify-content: space-around;
      border-bottom: 1px dotted @grey;
      img {
        width: 6rem;
        height: 3rem;
        border: 1px solid @grey;
      }
    }
    .sale-wrap {
      border-bottom: 1px solid @grey;
      p {
        display: flex;
        justify-content: space-around;
      }
    }
    .purchase-tip {
    }
  }
  footer {
    position: fixed;
    bottom: 0;
    width: 10rem;
    height: 40px;
   
    background: yellow;
    p {
    
      text-align: center;
    }
  }
}
</style>



