<template>
    <div class="playList">
        <main>
            <div v-for="(item,index) in CourseList" :key="index">
                <CourseListItem :courseItem="item" />
            </div>
        </main>
        <footer>
          <p @click="goCourseList">
          关闭
        </p>
    </footer>
    </div>
</template>
<script>
import CourseListItem from "components/CourseListItem";
export default {
  name: "playList",
  data() {
    return {
      CourseList: [
        { name: 1, age: "32fd33s434adsa", type: 1 },
        { name: 31, age: "d", type: 2 },
        { name: 31, age: "32ffs324fd43sa423dsa", type: 3 },
        { name: 14, age: "3233324sadsa", type: 1 },
        { name: 421, age: "322fd44333sadsa", type: 3 },
        { name: 421, age: "3233sadsa", type: 2 }
      ]
    };
  },
  components: {
    CourseListItem
  },
  created() {},
  props: {},
  methods: {
    goCourseList(){
      this.$router.push({
        path:'/CourseList'
      })
    }
  }
};
</script>
<style lang="less" scoped>
.playList {
  footer {
    position: fixed;
    bottom: 0;
    width: 10rem;
    height: 40px;
    background: yellow;
    p {
      width: 100%;
      text-align: center;
    }
  }
}
</style>




