import Vue from 'vue'
import Router from 'vue-router'
import CourseList from 'components/history/index.vue'
import audioCourse from 'components/history/audioCourse.vue'
import purchase from 'components/purchase.vue'
import videoCourse from 'components/history/videoCourse.vue'
import playList from 'components/history/playList.vue'
import share from 'components/history/share.vue'
Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            name: 'CourseList',
            component: CourseList
        },
        {
            path: '/CourseList',
            name: 'CourseList',
            component: CourseList
        },
        {
            path: '/audioCourse',
            name: 'audioCourse',
            component: audioCourse
        },
        {
            path: '/purchase',
            name: 'purchase',
            component: purchase
        },
        {
            path: '/videoCourse',
            name: 'videoCourse',
            component: videoCourse
        },
        {
            path: '/playList',
            name: 'playList',
            component: playList
        },
        {
            path: '/share',
            name: 'share',
            component: share
        },
    ]
})


