class utils {
    getQueryString = function(para) {
        console.log(para)
        let reg = new RegExp("(^|&)" + para + "=([^&]*)(&|$)");
        let r = window.location.search.substr(1).match(reg);
        if (r != null) return unescape(r[2]);
        return null;
    }
}
export default utils;


